      if($(".current").hasClass('active')){
        $(".current").removeClass('active');
        $(".sub-nav-model").hide();
        ModalHelper.beforeClose()
      }else {
        $(".current").addClass('active');

        $(".sub-nav-model").show();
        ModalHelper.afterOpen()
      }
